//import chroma from "chroma-js";
import Graph from "graphology";
import Sigma from "sigma";
//import ForceSupervisor from "graphology-layout-force/worker";
import forceAtlas2 from 'graphology-layout-forceatlas2';
import FA2Layout from 'graphology-layout-forceatlas2/worker';
import chroma from "chroma-js";

function extractNumber(str) {
    // 
    let numStr = str.replace(/\D/g, '');
    // 
    let num = parseInt(numStr, 10);
    return num;
}

// Retrieve the html document for sigma container
const container = document.getElementById("container");

// 
const graph = new Graph();
data.nodes.forEach(node => {
    graph.addNode(extractNumber(node.id), {
        country: node.country,
        field: node.field, 
        attend_as: node.attend_as, 
        label: [node.country, node.field, node.attend_as], 
        x: node.x, y: node.y, 
        country_color: node.country_color,
        attend_as_color: node.attend_as_color,
        field_color: node.field_color,
    });
});
data.edges.forEach(edge => {
    graph.addEdge(extractNumber(edge.source), extractNumber(edge.target), {timestamp: edge.timestamp});
});

// ForceAtlas2
const sensibleSettings = forceAtlas2.inferSettings(graph)
const layout = new FA2Layout(graph, {
    settings: sensibleSettings
  });
layout.start();

// Sigma
const renderer = new Sigma(graph, container);

// NodeEdge
const DEFAULT_ARGS = {
    nodesAlpha: 0.9,
    edgesAlpha: 0.7,
}
renderer.setSetting("nodeReducer", (node, context) => {
    const res = {...context};
    res.color = chroma(res.color || "#444").alpha(DEFAULT_ARGS.nodesAlpha).hex();
    return res;
})
renderer.setSetting("edgeReducer", (edge, context) => {
    const res = {...context};
    res.color = chroma(res.color || "#999").alpha(DEFAULT_ARGS.edgesAlpha).hex();
    return res;
})

//
// Drag'n'drop feature
// ~~~~~~~~~~~~~~~~~~~
//

// State for drag'n'drop
let draggedNode = null;
let isDragging = false;

// On mouse down on a node
//  - we enable the drag mode
//  - save in the dragged node in the state
//  - highlight the node
//  - disable the camera so its state is not updated
renderer.on("downNode", (e) => {
    isDragging = true;
    draggedNode = e.node;
    graph.setNodeAttribute(draggedNode, "highlighted", true);
    layout.stop();
});

// On mouse move, if the drag mode is enabled, we change the position of the draggedNode
renderer.getMouseCaptor().on("mousemovebody", (e) => {
    if (!isDragging || !draggedNode) return;

    // Get new position of node
    const pos = renderer.viewportToGraph(e);

    graph.setNodeAttribute(draggedNode, "x", pos.x);
    graph.setNodeAttribute(draggedNode, "y", pos.y);

    // Prevent sigma to move camera:
    e.preventSigmaDefault();
    e.original.preventDefault();
    e.original.stopPropagation();
});

// On mouse up, we reset the autoscale and the dragging mode
renderer.getMouseCaptor().on("mouseup", () => {
    if (draggedNode) {
    graph.removeNodeAttribute(draggedNode, "highlighted");
    }
    isDragging = false;
    draggedNode = null;
    layout.start();
});

// Disable the autoscale at the first down interaction
renderer.getMouseCaptor().on("mousedown", () => {
    if (!renderer.getCustomBBox()) renderer.setCustomBBox(renderer.getBBox());
});

// 
function colorize_nodes(){
    var selection = document.color_form.color_attribute;
    var selected_attribute = "";
    for(var i=0; i < selection.length; i++){
        if(selection[i].checked){
            selected_attribute = selection[i].value;
        }
    }
    renderer.setSetting("nodeReducer", (node, context) => {
        const res = {...context};
        if(selected_attribute == "attend_as"){
            res.color = context.attend_as_color;
        }else if(selected_attribute == "country"){
            res.color = context.country_color;
        }else if(selected_attribute == "field"){
            res.color = context.field_color;
        }
        res.color = chroma(res.color).alpha(DEFAULT_ARGS.nodesAlpha).hex();
        return res;
    })

}

const state = {};

function setHoveredNode(node) {
    if (node) {
        state.hoveredNode = node;
        state.hoveredNeighbors = new Set(graph.neighbors(node));
    }

    // Compute the partial that we need to re-render to optimize the refresh
    const nodes = graph.filterNodes((n) => n !== state.hoveredNode && !state.hoveredNeighbors?.has(n));
    const nodesIndex = new Set(nodes);
    const edges = graph.filterEdges((e) => graph.extremities(e).some((n) => nodesIndex.has(n)));

    if (!node) {
        state.hoveredNode = undefined;
        state.hoveredNeighbors = undefined;
    }

    // Refresh rendering
    renderer.refresh({
                partialGraph: {
                nodes,
                edges,
            },
            // We don't touch the graph data so we can skip its reindexation
            skipIndexation: true,
        });
}

// Bind graph interactions:
renderer.on("enterNode", ({ node }) => {
        setHoveredNode(node);
    });
renderer.on("leaveNode", () => {
        setHoveredNode(undefined);
    });

// Render nodes accordingly to the internal state:
// 1. If there is a hovered node, all non-neighbor nodes are greyed
renderer.setSetting("nodeReducer", (node, data) => {
        const res = { ...data };
        if (state.hoveredNeighbors && !state.hoveredNeighbors.has(node) && state.hoveredNode !== node) {
            res.label = "";
            res.color = "#DDD";
        }
        return res;
    });

// Render edges accordingly to the internal state:
// 1. If a node is hovered, the edge is hidden if it is not connected to the node
renderer.setSetting("edgeReducer", (edge, data) => {
    const res = { ...data };

    if (state.hoveredNode && !graph.hasExtremity(edge, state.hoveredNode)) {
        res.hidden = true;
    }

    return res;
});

window.colorize_nodes = colorize_nodes;